$spinner-size:                      50px !default;
$spinner-speed:                     1.5s !default;

// Spyre menu
$spyre-overlay-bg-color:            $secondary !default;
$spyre-overlay-bg-opacity:          .995 !default;
$spyre-nav-icons-color:             $white !default;
$spyre-nav-hover-icons-color:       $primary !default;
$spyre-nav-link-color:              $secondary !default;
$spyre-nav-link-hover-color:        $primary !default;
$spyre-nav-link-active-color:       $primary !default;
$searchbar-color:                   $white !default;
$searchbar-border-color:            $white !default;

// Footer
$footer-bg:                         $secondary !default;
$footer-bg-opacity:                 .97 !default;


// Syntax highlighter
$prism-bg-color:                    $gray-100 !default;
$prism-border-color:                $gray-300 !default;
$prism-border-radius:               0 !default;
$prism-font-family:                 Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace !default;
$prism-font-size:                   15px !default;
$prism-button-bg:                   $secondary !default;
$prism-button-color:                $white !default;

// Syntax highlighter
$carousel-nav-bg:                   rgba($white, .7) !default;
$carousel-nav-bg-hover:             rgba($white, .92) !default;
$carousel-nav-color:                rgba($secondary, .8) !default;
$carousel-nav-color-hover:          $secondary !default;

$carousel-primary-nav-bg:           rgba($primary, .7) !default;
$carousel-primary-nav-bg-hover:     rgba($primary, .92) !default;
$carousel-primary-nav-color:        rgba($white, .8) !default;
$carousel-primary-nav-color-hover:  $white !default;

$carousel-secondary-nav-bg:         rgba($secondary, .7) !default;
$carousel-secondary-nav-bg-hover:   rgba($secondary, .92) !default;
$carousel-secondary-nav-color:      rgba($white, .8) !default;
$carousel-secondary-nav-color-hover:$white !default;

$carousel-nav-disabled-opacity:     .3 !default;
$carousel-nav-font-size:            1.3125rem !default;

$carousel-dot-width:				10px !default;
$carousel-dot-height:			    10px !default;
$carousel-dot-rounded:			    30px !default;
$carousel-dot-margin: 			    5px 7px !default;
$carousel-dot-bg:		            rgba($white, .5)!default;
$carousel-dot-bg-active:            $white !default;

// Icons
$icon-bg-color:                     $white !default;
$icon-color:                        $white !default;

// Fancybox
$fancy-background-bg-color:         $gray-900 !default;
$fancy-background-bg-opacity:       .9 !default;
$fancy-toolbar-icon-bg:             $gray-900 !default;
$fancy-toolbar-icon-bg-opacity:     0 !default;
$fancy-toolbar-icon-color:          $white !default;
$fancy-caption-color:               $white !default;
$fancy-nav-bg:                      rgba($white, .7) !default;
$fancy-nav-bg-hover:                rgba($white, .92) !default;
$fancy-nav-color:                   rgba($secondary, .8) !default;
$fancy-nav-color-hover:              $secondary !default;
$fancy-nav-disabled-opacity:        .3 !default;

// Parallax
$disable-parallax-on-mobile:        true !default;

// Animations
$disable-animations-on-mobile:      true !default;